.p2p-order
  padding: 40px
  display: grid
  grid-template-columns: repeat(2, 1fr)
  align-items: start
  grid-gap: 20px
  border-radius: 20px
  border: 1px solid $borderColor
  background-color: $bgColor3
  &:not(:last-child)
    margin-bottom: 20px
  &-info
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 20px 10px
    &-title
      grid-column: span 2
    .item
      display: contents

  &-timer
    display: flex
    align-items: center
    justify-content: space-between
    .hours,
    .minutes,
    .seconds
      display: flex
      align-items: center
      justify-content: center
      size: 55px
      border-radius: 10px
      border: 1px solid $borderColor
      background-color: $bgColor3

  &-form
    padding: 25px 30px
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: rgba($whiteColor, 0.1)
    &:not(:last-child)
      margin-bottom: 20px
    & > .p2p-order-form
      padding: 15px 20px
    &-row
      display: flex
      align-items: center
      justify-content: space-between
      gap: 20px
      margin-bottom: 20px
      .ui-group
        margin-bottom: 0
    &-title
      position: relative
      display: flex
      align-items: center
      gap: 15px
      padding-bottom: 20px
      margin-bottom: 20px
      &::after
        position: absolute
        content: ''
        bottom: 0
        left: -30px
        size: calc(100% + 60px) 1px
        background-color: $borderColor
      img,
      .ico
        size: 40px
    .btn-primary
      max-width: 60%

  &-buttons
    display: flex
    align-items: center
    border-radius: 90px
    border: 1px solid $borderColor
    button
      padding: 8px 15px
      border-radius: 90px
      font-size: 16px
      font-weight: 400
      color: rgba($whiteColor, 0.5)
      background: none
      &.active
        color: $whiteColor
        background-image: linear-gradient(90deg, $blueColor 0%, $aquaColor 100%)

+r($lg)
  .p2p-order
    grid-template-columns: 1fr

+r($sm)
  .p2p-order
    padding: 20px

    &-info
      grid-template-columns: 1fr
      grid-gap: 20px
      &-title
        grid-column: unset
      .item
        & > *:nth-child(2)
          margin-top: -10px

    &-form
      padding: 15px 20px
      &-row
        flex-direction: column
        align-items: flex-start
        gap: 0
        .ui-group
          width: 100%
          &:not(:last-child)
            margin-bottom: 20px
      .btn-primary
        max-width: 100%
