.container {
  position: relative;
  z-index: 2;
  max-width: 1150px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}
.container.wide {
  max-width: 1630px;
}
.container.extended {
  max-width: 1390px;
}

.row {
  display: flex;
  gap: 20px;
}
.row.form-row {
  padding: 5px;
  border-radius: 90px;
  background-color: #141d26;
  gap: 5px;
}
.row.form-row:not(:last-child) {
  margin-bottom: 10px;
}
.row.column {
  flex-direction: column;
}
.row.wrap {
  flex-wrap: wrap;
}
.row.align-center {
  align-items: center;
}
.row.align-end {
  align-items: flex-end;
}
.row.justify-center {
  justify-content: center;
}
.row.justify-between {
  justify-content: space-between;
}
.row.justify-end {
  justify-content: flex-end;
}
.row.gap-0 {
  gap: 0;
}
.row.gap-10 {
  gap: 10px;
}

.swiper-slide {
  opacity: 0;
  transition: 0.25s all;
}
.swiper-slide-visible {
  opacity: 1;
}
.swiper-pagination {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.swiper-pagination-bullet {
  size: 6px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
}
.swiper-pagination-bullet-active {
  background: linear-gradient(180deg, #f73e5d 0%, rgba(247, 62, 93, 0) 100%);
}

.submenu {
  display: none;
  position: absolute;
  top: calc(100% + 18px);
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #cb9f37;
  border-radius: 0 0 20px 20px;
  list-style-type: none;
  background-color: #22313e;
  overflow: hidden;
}
.submenu li {
  padding: 20px;
}
.submenu li:not(:last-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.submenu li:hover {
  background: linear-gradient(270.77deg, rgba(250, 226, 128, 0) -5.11%, #EED780 43.1%, #C5952A 99.34%);
}
.submenu li a {
  display: flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
}
.submenu li a .ico {
  flex: 0 0 20px;
  color: #fff;
}
.submenu li a span {
  flex: 1 1 auto;
  text-align: left;
  color: #fff;
}
.submenu .btn {
  border-radius: 0 0;
}

.p2p .tabs-nav {
  display: flex;
  border-radius: 90px;
  background-color: rgba(81, 195, 235, 0.2);
}
.p2p .tabs-nav a {
  display: block;
  padding: 8px 20px;
  font-size: 16px;
  color: #fff;
  border-radius: 90px;
}
.p2p .tabs-nav a.active {
  background-image: linear-gradient(90deg, #298be7 0%, #70efee 100%);
}
.p2p .tabs-item {
  display: none;
}
.p2p .tabs-item.active {
  display: block;
}
.settings .tabs-nav {
  padding: 30px 40px;
  border: 1px solid #4b555f;
  border-radius: 20px;
  background: #1e2b37;
}
.settings .tabs-nav a {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 15px 0;
}
.settings .tabs-nav a::before {
  position: absolute;
  content: "";
  size: calc(100% + 80px) 100%;
  top: 0;
  left: -40px;
  background-image: linear-gradient(90deg, #f73e5d 0%, rgba(247, 62, 93, 0) 100%);
  opacity: 0;
}
.settings .tabs-nav a:hover::before, .settings .tabs-nav a.active::before {
  opacity: 1;
}
.settings .tabs-nav a span,
.settings .tabs-nav a .ico {
  position: relative;
  color: #fff;
}
.settings .tabs-nav a .ico {
  size: 20px;
}
.settings .tabs-content {
  padding: 25px 30px;
  border: 1px solid #4b555f;
  border-radius: 20px;
  background: #1e2b37;
}
.settings .tabs-item {
  display: none;
}
.settings .tabs-item.active {
  display: block;
}

table.p2p-table-layout {
  white-space: nowrap;
  width: 100%;
  border-collapse: collapse;
}
table.p2p-table-layout .btn {
  min-width: 120px;
  max-width: 120px;
}
table.p2p-table-layout thead {
  border-radius: 90px;
  background-color: #1f3345;
}
table.p2p-table-layout thead th {
  padding: 10px;
  font-size: 16px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
  text-align: left;
}
table.p2p-table-layout thead th:first-child {
  padding-left: 30px;
  border-radius: 90px 0 0 90px;
}
table.p2p-table-layout thead th:last-child {
  width: 120px;
  padding-right: 30px;
  border-radius: 0 90px 90px 0;
}
table.p2p-table-layout tbody tr {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
table.p2p-table-layout tbody tr:last-child {
  border-bottom: none;
}
table.p2p-table-layout tbody th {
  text-align: left;
}
table.p2p-table-layout tbody td, table.p2p-table-layout tbody td a {
  text-align: left;
  padding: 20px 10px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
}
table.p2p-table-layout tbody td:last-child, table.p2p-table-layout tbody td a:last-child {
  padding: 20px 0px 20px 10px;
}
table.p2p-table-layout tbody td.complete, table.p2p-table-layout tbody td a.complete {
  color: #00ba34;
}
table.p2p-table-layout tbody td.denied, table.p2p-table-layout tbody td a.denied {
  color: #BF0023;
}
table.p2p-table-layout tbody td.active button, table.p2p-table-layout tbody td a.active button {
  position: relative;
  padding: 0;
  width: fit-content;
  font-size: 16px;
  font-weight: 600;
  color: #298be7;
  background: none;
}
table.p2p-table-layout tbody td.active button::after, table.p2p-table-layout tbody td a.active button::after {
  position: absolute;
  content: "";
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
  size: 15px;
  background-image: svg-load("../icons/svg-mono/pencil.svg", fill=rgba(255, 255, 255, 0.5));
  background-size: cover;
}
table.p2p-table-layout tbody td.stoped button, table.p2p-table-layout tbody td a.stoped button {
  position: relative;
  padding: 0;
  width: fit-content;
  font-size: 16px;
  font-weight: 600;
  color: #FFAA20;
  background: none;
}
table.p2p-table-layout tbody td.stoped button::after, table.p2p-table-layout tbody td a.stoped button::after {
  position: absolute;
  content: "";
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
  size: 15px;
  background-image: svg-load("../icons/svg-mono/pencil.svg", fill=rgba(255, 255, 255, 0.5));
  background-size: cover;
}
table.p2p-table-layout tbody td a {
  display: block;
  padding: 0 !important;
}

.get-money-value {
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 600;
  color: #fff;
}
.get-money-slider {
  margin-bottom: 20px;
}
.get-money-buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.get-money-buttons button {
  flex: 1 1 auto;
  padding: 20px;
  border-radius: 20px;
  background-color: rgba(57, 162, 233, 0.2);
}

.tooltip-container {
  position: relative;
  height: 18px;
}
.tooltip-container > span, .tooltip-container > a {
  cursor: pointer;
}
.tooltip-container:hover > span .ico, .tooltip-container:hover > a .ico {
  color: #70efee;
}
.tooltip-container:hover .tooltip-items {
  display: block;
}
.ui-group label .tooltip-container {
  position: static;
}
.tooltip-items {
  position: absolute;
  z-index: 3;
  top: calc(100% + 10px);
  left: 0;
  display: none;
  width: 335px;
  padding: 10px 15px;
  border-radius: 20px;
  background-color: #0c2234;
  box-shadow: 5px 5px 55px rgba(255, 255, 255, 0.3);
}
.p2p-table-layout .tooltip-items, .p2p-table-grid .tooltip-items {
  top: unset;
  bottom: 100%;
}
.partners-item .tooltip-items {
  width: fit-content;
}
.tooltip-items .row:not(:last-child) {
  margin-bottom: 10px;
}
.tooltip-items .title,
.tooltip-items .value,
.tooltip-items .value-2 {
  flex: 0 0 calc(50% - 10px);
}
.tooltip-items .title {
  font-size: 14px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(180deg, #298be7 0%, #70efee 100%);
}
.tooltip-items .value, .tooltip-items .value-2 {
  font-size: 14px;
  font-weight: 700;
  color: #fff;
}
.tooltip-items .value-2 {
  font-weight: 400;
}

@media only screen and (max-width: 991px) {
  .settings .tabs-nav {
    border: none;
    padding: 0;
  }
  .settings .tabs-nav nav {
    display: none;
  }
}
@media only screen and (max-width: 575px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .p2p .tabs-nav a {
    display: none;
  }
}
