a.dot-decoration {
  position: relative;
  padding: 20px 10px 20px 30px;
  font-size: 16px;
  font-weight: 600;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(180deg, #298be7 0%, #70efee 100%);
}
.p2p-order a.dot-decoration {
  display: block;
  padding: 0 0 0 10px;
  line-height: 24px;
}
.p2p-order a.dot-decoration::before {
  left: 0;
}
a.dot-decoration::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
