.referal-steps
  padding: 0
  margin-bottom: 230px
  background: none

  &-image
    position: relative
    max-width: calc(100% - 130px)
    margin: 30px auto 0
    width: 100%
    &::before
      content: ''
      display: block
      padding-top: 80%
    &::after
      position: absolute
      content: ''
      left: 50%
      bottom: -6px
      transform: translateX(-50%)
      size: calc(100% + 40px) 6px
      background-color: rgba($whiteColor, 0.2)
    img
      position: absolute
      bottom: 0
      max-height: 100%
      height: auto
      width: 100%

  &-title
    margin: 30px 0 20px
    font-size: 18px
    font-weight: 700
    color: $whiteColor
    text-align: center
  &-text
    padding: 16px 20px
    border-top: 1px solid $borderColor
    .text-item
      &:not(:last-child)
        margin-bottom: 8px

  .swiper-slide
    height: auto
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: rgba($whiteColor, 0.1)

  .btn
    margin: 0 auto
    width: fit-content
    transform: translateY(-50%)

  .swiper-pagination
    display: none

+r($xl)
  .referal-steps
    margin-bottom: 90px
    .swiper-pagination
      display: flex

+r($sm)
  .referal-steps
    margin-bottom: 60px
