// buttons
button
  -webkit-appearance: none
  border: 0
  box-shadow: none
  cursor: pointer
  &:focus
    outline: none

.btn
  position: relative
  display: flex
  align-items: center
  justify-content: center
  gap: 10px
  padding: 14px 16px
  min-width: 160px
  width: 100%
  font-size: 16px
  font-weight: 400
  line-height: 18px
  color: $whiteColor
  text-align: center
  border-radius: 900px
  cursor: pointer
  box-shadow: none
  transition: all $transitionSpeed $ease

  &[type="submit"]
    &:disabled
      opacity: 0.5
      pointer-events: none
      user-select: none

  &-blue
    background: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    &:hover
      box-shadow: 0 0 5px $blueColor

  &-primary
    background: linear-gradient(154.26deg, $redColor 16.27%, rgba(247, 62, 93, 0) 154.75%)
    background: linear-gradient(154.26deg, #FCD535 16.27%, #AD8F2A 154.75%)
    &.shiny
      box-shadow: 5px 5px 55px rgba($redColor, 0.6)
      &:hover
        box-shadow: 5px 5px 25px rgba($redColor, 0.6)
    &:hover
      box-shadow: 0 0 5px $redColor

  &-secondary
    background: #080B0E
    border: 1px solid #EBC836

    &:hover
      box-shadow: 0 0 5px #fcd535

  &-gold
    background-image: linear-gradient(264.01deg, #FAE280 -7.07%, #EED780 42.6%, #C5952A 97.27%)

  &-black
    border: 1px solid $redColor
    background-color: $blackColor
    box-shadow: 5px 5px 55px rgba($redColor, 0.6)
    &:hover
      box-shadow: 5px 5px 25px rgba($redColor, 0.6)

  &-control
    position: relative
    padding: 10px
    background: none
    .ico
      size: 20px
      color: rgba($whiteColor, 0.2)
    &.alarm
      &:hover
        & > .ico
          color: $yellowColor !important
      .notice
        position: absolute
        top: 8px
        right: 12px
        size: 9px
        display: flex
        align-items: center
        justify-content: center
        border-radius: 50%
        font-size: 5px
        line-height: 7px
        color: $whiteColor
        background: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    &.menu
      &:hover
        & > .ico
          color: $whiteColor
        &::before
          position: absolute
          content: ''
          top: 50%
          left: 50%
          transform: translate(-50%, -25px)
          z-index: -1
          border-radius: 20px 20px 0 0
          size: 100% calc(100% + 23px)
          background: linear-gradient(154.26deg, #FCD535 16.27%, rgba(252, 213, 53, 0) 154.75%)
        &::after
          position: absolute
          content: ''
          top: 100%
          left: 0
          size: 100% 25px
        & > .submenu
          display: block

  &-filter
    padding: 0
    flex: 0 0 45px
    display: flex
    align-items: center
    justify-content: center
    size: 45px
    background-color: $bgColor2
    border-radius: 50%
    .ico
      size: 18px
      color: $whiteColor

  &-reset
    padding: 0
    display: grid
    grid-template-columns: 20px auto
    align-items: center
    gap: 10px
    background: none
    .ico
      flex: 0 0 20px
      color: rgba($whiteColor, .5)
    span
      flex: 1 0 auto
      font-size: 18px
      font-weight: 600
      text-align: left
      color: rgba($whiteColor, 0.5)
      text-decoration: underline
    +r($sm)
      align-items: stretch
      .ico
        transform: translateY(2px)

  &-count
    position: relative
    display: flex
    align-items: center
    justify-content: center
    size: 45px
    border-radius: 50%
    border: 1px solid rgba($whiteColor, 0.1)
    background: none
    &.minus
      &::before
        position: absolute
        content: ''
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        size: 17px 3px
        background-color: #298BE7
    &.plus
      &::before
        position: absolute
        content: ''
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        size: 17px 3px
        background-color: #298BE7
      &::after
        position: absolute
        content: ''
        top: 50%
        left: 50%
        transform: translate(-50%, -50%) rotate(90deg)
        size: 17px 3px
        background-color: #298BE7
