.binary-marketing
  background: none
  padding: 0

  &__box
    background: rgba($whiteColor, 0.1)
    border-radius: 20px
    padding: 27px 40px 63px
    color: $whiteColor

  &__title
    font-size: 40px
    line-height: 54px
    text-align: center
    margin-bottom: 34px

  &__row
    display: flex
    gap: 30px
    margin-bottom: 30px

    +r($sm)
      flex-wrap: wrap

  &__col
    flex-grow: 1
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #09121A
    border: 1px solid #4B555F
    padding: 20px
    text-align: center
    border-radius: 20px

    +r($sm)
      width: 100%

    &-title
      font-size: 18px
      line-height: 140%

    &-value
      font-size: 40px
      line-height: 54px

.marketing-example
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #09121A
  border: 1px solid #4B555F
  padding: 20px
  border-radius: 20px
  line-height: 22px

  &__title
    font-size: 18px
    line-height: 140%
    margin-bottom: 10px

  p
    margin: 0 0 40px

  &__infographic-row
    display: flex
    justify-content: space-between

    &_level2
      margin: 45px -10px 0

  &__infographic-col1,
  &__infographic-col3
    width: 43%
    text-align: center
    position: relative

  &__infographic-col1
    &::before,
    &::after
      content: ''
      position: absolute
      top: 195px
      width: 102px
      height: 101px
      background: url(../img/binary-marketing/arrow.svg) 50% 50%/contain no-repeat

    &::before
      left: 58px
      transform: rotate(-33deg)

    &::after
      right: 58px
      transform: rotate(33deg) scaleX(-1)

  &__infographic-col2
    width: 14%
    min-width: 124px
    text-align: center
    position: relative

    &::before,
    &::after
      content: ''
      position: absolute
      top: 27px
      width: 102px
      height: 101px
      background: url(../img/binary-marketing/arrow.svg) 50% 50%/contain no-repeat

    &::before
      left: -125px

    &::after
      right: -125px
      transform: scaleX(-1)

  &__infographic-col3
    &::after
      content: ''
      position: absolute
      top: 195px
      right: 58px
      width: 69px
      height: 100px
      background: url(../img/binary-marketing/line.svg) 50% 50%/contain no-repeat

  &__infographic-row_level2 &__infographic-col1::before,
  &__infographic-row_level2 &__infographic-col1::after,
  &__infographic-row_level2 &__infographic-col2::before,
  &__infographic-row_level2 &__infographic-col2::after,
  &__infographic-row_level2 &__infographic-col3::before,
  &__infographic-row_level2 &__infographic-col3::after
    display: none

  &__col-title
    display: block
    margin: 22px 0 27px
    color: rgba($whiteColor, 0.4)
    font-size: 30px
    line-height: 41px
    font-weight: 600
    text-align: left

  &__infographic-col3 &__col-title
    text-align: right

  &__person
    display: block
    width: 124px
    height: auto
    border-radius: 50%
    border: 17px solid rgba($whiteColor, 0.1)
    margin: 0 auto 10px
    position: relative
    z-index: 2

  &__infographic-col2 &__person
    margin-bottom: 0

  &__name
    font-size: 30px
    line-height: 41px
    font-weight: 600

  &__value
    font-size: 40px
    line-height: 54px
    font-weight: 600
    display: block
    margin-top: 10px
    padding-top: 7px
    position: relative

    &::before
      content: ''
      position: absolute
      top: 0
      left: 50%
      transform: translateX(-50%)
      height: 2px
      background: linear-gradient(180deg, #298BE7 0%, #70EFEE 100%)
      border-radius: 2px

  &__infographic-col2 &__value::before
    width: 275%

  &__infographic-row_level2 &__infographic-col2 &__value::before
    width: 150%

  &__infographic-row_level2 &__person
    width: 70px
    border-width: 10px

  &__infographic-row_level2 &__name
    font-size: 22px
    line-height: 30px

  &__infographic-row_level2 &__value
    margin-top: 34px

  &__total
    text-align: center

  &__total &__value
    margin-top: 33px
    padding-top: 7px

    &::before
      width: 87%
