// titles
h1, h2, h3, h4, h5, h6, p, a, ul, li, ol, span
  margin: 0
  padding: 0

a
  text-decoration: none
  &.dot-decoration
    position: relative
    padding: 20px 10px 20px 30px
    font-size: 16px
    font-weight: 600
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    .p2p-order &
      display: block
      padding: 0 0 0 10px
      line-height: 24px
      &::before
        left: 0
    &::before
      position: absolute
      content: ''
      top: 50%
      left: 20px
      transform: translateY(-50%)
      size: 4px
      border-radius: 50%
      background-color: $greenColor
  &.star-decoration
    position: relative
    padding: 20px 10px 20px 30px
    font-size: 16px
    font-weight: 600
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    &::before
      position: absolute
      content: ''
      top: 50%
      left: 10px
      transform: translateY(-50%)
      size: 13px
      background-image: svg-load('../icons/svg-mono/star.svg')
  .text-item &
    color: inherit
    text-decoration: underline

.link-grad
  font-size: 16px
  font-weight: 400
  -webkit-background-clip: text
  -webkit-text-fill-color: transparent
  background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
  &.red
    background-image: linear-gradient(154.26deg, $redColor 16.27%, rgba(247, 62, 93, 0) 154.75%)

.section-title
  font-size: 40px
  font-weight: 700
  line-height: 54px
  color: $whiteColor
  &.big
    font-size: 45px
    line-height: 60px
    line-height: normal
  &.small
    font-size: 30px
    font-weight: 700
    line-height: normal
  .gradient
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    &-1
      @extend .gradient
      background-image: linear-gradient(90deg, $redColor 0%, $blueColor 100%)
    &-2
      @extend .gradient
      background-image: linear-gradient(-90deg, $redColor 0%, $blueColor 100%)

.title-item
  font-size: 18px
  color: $whiteColor
  &-2
    font-size: 25px
    font-weight: 600
    color: $whiteColor
  &-3
    font-size: 30px
    font-weight: 600
    color: $whiteColor

.text-item
  font-size: 16px
  font-weight: 400
  line-height: 24px
  color: rgba($whiteColor, 0.5)
  &-2
    font-size: 18px
    font-weight: 600
    color: $whiteColor
  &-3
    font-size: 20px
    font-weight: 600
    color: $whiteColor
  &-4
    font-size: 16px
    font-weight: 600
    color: $whiteColor

.uppercase
  text-transform: uppercase

.text-center
  text-align: center

.text-left
  text-align: left

.text-right
  text-align: right

.fz-12
  font-size: 12px !important
  line-height: normal

.fz-14
  font-size: 14px !important
  line-height: normal

.fz-18
  font-size: 18px !important

.fw-600
  font-weight: 600 !important

.fw-700
  font-weight: 700 !important

+r($sm)
  .section-title
    font-size: 25px
    line-height: 25px
    &.small
      font-size: 20px

.gradient-text
  background: linear-gradient(180deg, #298BE7 0%, #70EFEE 100%)
  -webkit-background-clip: text
  -webkit-text-fill-color: transparent
  background-clip: text
  text-fill-color: transparent

.gradient-text-gold
  background: linear-gradient(154.26deg, #FCD535 16.27%, rgba(252, 213, 53, 0) 154.75%)
  -webkit-background-clip: text
  -webkit-text-fill-color: transparent
  background-clip: text
  text-fill-color: transparent
