.invest-account-slider
  padding: 0
  margin-bottom: 60px
  background: none
  .swiper
    &-initialized
      position: relative
    &-slide
      padding: 20px
      border-radius: 20px
      border: 1px solid $borderColor
      background: $bgColor5
      &-top
        margin-bottom: 20px
        display: flex
        align-items: center
        gap: 10px
      &-middle
        padding: 20px 0
        border-top: 1px solid rgba($whiteColor, 0.1)
        border-bottom: 1px solid rgba($whiteColor, 0.1)
      &-bottom
        margin-top: 20px
        details
          cursor: pointer
          &[open]
            summary
              margin-bottom: 20px
            .icon
              transform: rotate(180deg)
          summary
            display: flex
            align-items: center
            justify-content: center
            gap: 10px
            .icon
              size: 13px 8px
              background-size: cover
              background-image: svg-load('../icons/svg-inline/arrow-down.svg')
              transition: 0.25s all
    &-button
      &-next,
      &-prev
        position: absolute
        size: 15px 26px
        top: -58px
        cursor: pointer
      &-next
        right: 0
        background-image: svg-load('../icons/svg-inline/arrow-next.svg')
      &-prev
        right: 50px
        background-image: svg-load('../icons/svg-inline/arrow-prev.svg')

+r($sm)
  .invest-account-slider

    .swiper
      &-button
        &-next,
        &-prev
          display: none
