// micromodal
.modal
  &-overlay
    position: fixed
    top: 0
    left: 0
    right: 0
    bottom: 0
    z-index: 99
    background: rgba(0, 0, 0, 0.6)
    display: flex
    justify-content: center
    align-items: center
    overflow-y: auto
  &-container
    position: relative
    width: 100%
    background-color: $bgColor4
    padding: 65px 45px
    max-width: 1110px
    width: 100%
    max-height: calc(100vh - 40px)
    border: 1px solid $borderColor
    border-radius: 20px
    box-sizing: border-box
    overflow-y: auto
    overflow-x: hidden
    &-2
      @extend .modal-container
      max-width: 730px
      padding: 50px 40px
    &-3
      @extend .modal-container
      padding: 30px 30px 0
      background-color: $bgColor3
  &-header
    margin-bottom: 35px
    display: flex
    justify-content: center
    .section-title
      text-align: center
  &-form
    &-row
      margin-top: 35px
      display: flex
      gap: 30px
      & > *
        flex: 0 1 calc(50% - 15px)
    &-grid
      display: grid
      grid-template-columns: 5fr 7fr
      grid-gap: 30px
      & > *
        margin-bottom: 0 !important
    &-bottom
      margin-top: 30px
      display: flex
      align-items: center
      justify-content: space-between
    &-column
      display: flex
      flex-direction: column
    &-attention
      grid-column: 2
      grid-row: 1 / span 2
      padding: 20px
      border-radius: 20px
      background-color: $bgColor2
      .link-grad
        display: block
        width: fit-content
        margin-left: auto
    &-notice
      margin-top: 20px
      padding-top: 20px
      border-top: 1px solid rgba($whiteColor, 0.5)
      display: flex
      gap: 20px
      .ico
        flex: 0 0 34px
        size: 34px
        color: rgba($whiteColor, 0.5)
    .btn-modal
      padding: 5px 20px
      &-2
        width: fit-content
        margin-left: auto

  &-close
    position: absolute
    z-index: 3
    right: 10px
    top: 0px
    padding: 10px
    background: transparent
    border: 0
    cursor: pointer
    font-size: 0
    color: rgba($whiteColor, 0.5)
    transition: color .25s $ease
    &:hover
      color: rgba($whiteColor, 1)
    .ico
      font-size: 20px

+r($lg)
  .modal
    &-container
      max-width: 100%
      margin-right: 15px
      margin-left: 15px
      padding: 25px 30px

+r($md)
  .modal
    &-form
      &-row
        flex-direction: column
      &-bottom
        flex-direction: column
        align-items: stretch
        justify-content: stretch
        gap: 15px
    .btn-modal
      padding: 5px 20px
      width: 100%

+r($sm)
  .modal
    &-form
      &-grid
        grid-template-columns: 1fr
        grid-gap: 20px
      &-attention
        grid-column: unset
        grid-row: unset
      &-notice
        flex-direction: column
        gap: 10px
    &-container
      padding: 35px 20px 15px

@keyframes mmfadeIn
  from
    opacity: 0
  to
    opacity: 1

@keyframes mmfadeOut
  from
    opacity: 1
  to
    opacity: 0

@keyframes mmslideIn
  from
    transform: translateY(15%)
  to
    transform: translateY(0)

@keyframes mmslideOut
  from
    transform: translateY(0)
  to
    transform: translateY(-10%)

.micromodal-slide
  display: none
  &.is-open
    display: block

  &[aria-hidden="false"]
    .modal__overlay
      animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1)

  &[aria-hidden="true"]
    .modal__overlay
      animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1)

  .modal__container, .modal__overlay
    will-change: transform
