[class*="-block"]
  display: block

.v-hidden
  visibility: hidden

.border
  &-bottom
    border-bottom: 1px solid rgba($whiteColor, 0.1)
  &-top
    border-top: 1px solid rgba($whiteColor, 0.1)

.decoration
  position: absolute
  pointer-events: none
  z-index: 1
  &-1
    @extend .decoration
    size: 900px
    background: radial-gradient(36.17% 36.17% at 50% 50%, rgba(41, 139, 231, 0.4) 0%, rgba(41, 139, 231, 0) 100%)
  &-2
    @extend .decoration
    size: 900px
    background: radial-gradient(36.17% 36.17% at 50% 50%, rgba(247, 62, 93, 0.5) 0%, rgba(247, 62, 93, 0) 100%)

.size-30
  size: 30px
