.p2p
  padding: 190px 0
  background: none

  &-page
    min-height: 100vh
    background: #070707 url(../img/account-bg.webp) 50% 0/cover no-repeat

  &-row
    &-title
      +row-acenter-jbetween
      margin-bottom: 40px
    &-statistic
      +row-acenter-jcenter
      flex-wrap: wrap
      margin-bottom: 40px

  &-buttons
    display: flex
    align-items: center
    gap: 20px
    .btn
      padding: 5px 50px

  &-info-item
    flex: 0 0 30%
    padding: 20px
    text-align: center
    border-radius: 20px
    background-color: $bgColor3

+r($lg)
  .p2p
    &-row
      &-title
        flex-direction: column
        align-items: stretch
        justify-content: stretch
        margin-bottom: 30px
      &-statistic
        *
          flex: 0 1 calc( 50% - 10px )

+r($sm)
  .p2p
    &-buttons
      flex-direction: column
    &-row
      &-statistic
        flex-direction: column
        align-items: unset
        *
          flex: 1 1 auto
