.wallet
  padding: 190px 0
  background: none

  &-page
    background-size: cover
    background-position: center
    background-image: url(../img/banner-bg.webp)

  &-panel
    &-top
      padding: 30px
      border-radius: 20px 20px 0 0
      border: 1px solid $borderColor
      border-bottom: none
      background-color: $bgColor3
      .btn
        max-width: fit-content
        padding: 8px 20px
        .ico
          size: 30px
        .value
          font-size: 18px
          font-weight: 600
          text-transform: uppercase
        .name
          text-align: left
    &-middle
      padding: 30px
      background-color: $bgColor4
    &-bottom
      padding: 30px
      border-radius: 0 0 20px 20px
      border: 1px solid $borderColor
      border-top: none
      background-color: $bgColor3
      overflow: auto

  &-buttons
    display: flex
    align-items: center
    flex-wrap: wrap
    gap: 20px

  &-info
    display: flex
    align-items: center
    flex-wrap: wrap
    gap: 20px 80px
    &-copy
      padding: 0
      margin: 0 7px
      size: 20px
      display: inline-block
      background: none
    &-number
      padding: 4px 10px
      margin: -4px 7px
      border-radius: 900px
      display: inline-block
      color: $whiteColor
      background-image: linear-gradient(90deg, $redColor 0%, rgba(247, 62, 93, 0) 150%)

  &-table
    min-width: 992px
    &-head
      padding: 10px 30px
      display: grid
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr))
      grid-gap: 15px
      border-radius: 90px
      background-color: $bgColor4
      .item
        font-size: 16px
        font-weight: 400
        color: rgba($whiteColor, 0.5)
    &-body
      details
        cursor: pointer
        border-bottom: 1px solid rgba($whiteColor, 0.2)
        &:last-child
          margin-bottom: -30px
          border-bottom: none
        &[open]
          summary
            &::before
              transform: translateY(-50%) rotate(180deg)
        summary
          position: relative
          padding: 30px
          display: grid
          grid-template-columns: repeat(auto-fit, minmax(150px, 1fr))
          grid-gap: 15px
          &::before
            position: absolute
            content: ''
            top: 50%
            right: 30px
            transform: translateY(-50%)
            size: 13px 8px
            background-image: svg-load('../icons/svg-mono/arrow-down.svg', fill=$whiteColor)

          .item
            display: flex
            align-items: center
            justify-content: flex-start
            gap: 8px
            font-size: 16px
            font-weight: 400
            color: $whiteColor
            img
              size: 20px

    &-content
      padding: 20px 30px
      border-top: 1px solid rgba($whiteColor, 0.2)
      .item
        font-size: 18px
        color: rgba($whiteColor, 0.5)
        &:not(:last-child)
          margin-bottom: 20px
        span
          font-weight: 600
          color: $whiteColor

+r($lg)
  .wallet

    &-panel
      &-middle
        border-radius: 0 0 20px 20px
      &-bottom
        margin-top: 20px
        border-radius: 20px
        border-top: 1px solid $borderColor

+r($sm)
  .wallet
    &-buttons,
    &-info
      flex-direction: column
      align-items: stretch
