.referal
  padding: 0
  margin-bottom: 70px
  background: none

  &-page
    padding: 190px 0 190px 90px
    background: #070707 url(../img/account-bg.webp) 50% 0/cover no-repeat

    +r($hd)
      padding-left: 0

  &-row
    display: grid
    grid-template-columns: repeat(12, 1fr)
    align-items: center

  &-title
    grid-column: span 6

  &-link
    &-container
      grid-column: 8 / span 5
      padding: 30px
      border-radius: 20px
      background: linear-gradient(154.26deg, #FCD535 16.27%, rgba(252, 213, 53, 0) 154.75%)

      .ui-input
        background-color: rgba($whiteColor, 0.3)

+r($lg)
  .referal
    margin-bottom: 60px
    &-row
      grid-template-columns: 1fr
      grid-gap: 20px
    &-title
      grid-column: unset
    &-link
      &-container
        padding: 20px 10px
        grid-column: unset
        .title-item-2
          text-align: center
