.notification
  padding: 190px 0
  min-height: 100vh
  background-image: url(../img/account-bg.webp)
  background-size: cover
  background-position: center top

  &-wrapper
    padding: 0 40px
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: $bgColor3

  &-item
    padding: 40px 0
    display: flex
    align-items: center
    flex-wrap: wrap
    justify-content: space-between
    gap: 10px
    &:not(:last-child)
      border-bottom: 1px solid rgba($whiteColor, 0.1)
