.control-ballance
  &-list
    min-width: 992px
    .title-row,
    .content-row
      display: grid
      grid-template-columns: repeat(3, 1fr)
      &:not(:last-child)
        margin-bottom: 20px
        padding-bottom: 20px
        border-bottom: 1px solid rgba($whiteColor, 0.2)
    .content-row-2
      display: grid
      grid-template-columns: 1fr 2fr 3fr 1fr 2fr 3fr 3fr
      align-items: center
      grid-gap: 20px
      min-height: 66px
      &:not(:last-child)
        margin-bottom: 20px
        padding-bottom: 20px
        border-bottom: 1px solid rgba($whiteColor, 0.1)
      .btn
        max-height: 45px
    .cell
      font-size: 18px
      font-weight: 600
      color: $whiteColor
