.invest-account
  padding: 0
  margin-bottom: 80px
  background: none
  &-page
    padding: 150px 0 190px
    background-image: url(../img/account-bg.webp)
    background-size: cover

  &-wrapper
    display: grid
    grid-template-columns: 1fr 2fr
    align-items: start
    grid-gap: 30px

  &-item
    padding: 20px
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: $bgColor5

    &__text
      line-height: 22px
      margin-bottom: 18px

  &-info
    padding: 20px
    border-radius: 20px
    border: 1px solid #4b555f
    background-color: rgba($whiteColor, 0.1)
    &-top
      margin-bottom: 20px
      display: flex
      align-items: center
      gap: 10px
    &-middle
      padding: 20px 0
      border-top: 1px solid rgba($whiteColor, 0.1)
      border-bottom: 1px solid rgba($whiteColor, 0.1)
      display: flex
      justify-content: space-between
      gap: 50px
      & > *
        flex: 1 0 calc(50% - 25px)
    &-bottom
      margin-top: 20px
      details
        cursor: pointer
        &[open]
          summary
            margin-bottom: 20px
          .icon
            transform: rotate(180deg)
        summary
          display: flex
          align-items: center
          justify-content: center
          gap: 10px
          .icon
            size: 13px 8px
            background-size: cover
            background-image: svg-load('../icons/svg-inline/arrow-down.svg')
            transition: 0.25s all

  &-row
    display: flex
    align-items: center
    gap: 18px

  &-stat
    .item
      display: grid
      grid-template-columns: 2fr 1fr
      grid-gap: 10px

  .user
    &-data
      display: grid
      grid-template-columns: 1fr 2fr
      gap: 20px
      .image
        // size: 90px
        border-radius: 50%
        border: 4px solid $goldColor
        overflow: hidden
      .btn
        grid-column: span 2
      .percent
        display: flex
        align-items: center
        gap: 5px
        &-number
          size: 40px
          display: flex
          align-items: center
          justify-content: center
          font-size: 16px
          font-weight: 700
          color: $whiteColor
          border-radius: 50%
          background-image: linear-gradient(264.01deg, #FAE280 -7.07%, #EED780 42.6%, #C5952A 97.27%)

  .partners
    margin-top: 20px
    &-item
      display: grid
      grid-template-columns: auto 1fr auto
      align-items: center
      grid-gap: 15px
      &:not(:last-child)
        margin-bottom: 20px
      span
        display: block
        font-size: 14px
        color: $whiteColor
      .progressbar
        position: relative
        padding: 2px 0
        border-radius: 90px
        background-color: rgba($whiteColor, 0.1)
        text-align: center
        span
          position: relative
          z-index: 2
        .progress
          position: absolute
          top: 0
          left: 0
          height: 100%
          border-radius: 90px
          background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
        .tooltip-items
          max-width: 170px

    &-status
      .image
        margin: 40px 0
        &.disabled
          opacity: 0.5
      .btn
        margin: 0 -20px -20px -20px
        width: calc(100% + 40px)
        border-radius: 0 0 20px 20px

        background: linear-gradient(#8B8B8B -138.41%, #222534 64.9%, #162534 288.66%)
        border: 1px solid #FCD535

+r($lg)
  .invest-account

    &-wrapper
      grid-template-columns: 1fr

    &-stat
      .item
        grid-template-columns: 1fr
        &:not(:last-child)
          margin-bottom: 20px

    .user
      display: grid
      grid-template-columns: repeat(2, 1fr)
      grid-gap: 30px
      .partners
        margin-top: 0
        &-status
          display: none

+r($md)
  .invest-account

    &-row
      flex-direction: column
      align-items: flex-start

    &-info
      &-middle
        flex-direction: column

    .user
      display: block
      .partners
        margin-top: 20px
        &-status
          display: block
