.controls
  min-height: 100vh
  padding: 50px 0 160px
  background-image: url(../img/control-bg.webp)
  background-size: cover

  &-wrapper
    padding: 30px
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: $bgColor3
    overflow: auto
    &:not(:last-child)
      margin-bottom: 30px

+r($hd)
  .controls
    padding: 120px 0
