.ui-file
  position: relative
  input[type="file"]
    position: absolute
    size: 0.1px
    visibility: hidden

  label
    display: flex
    align-items: center
    gap: 5px
    font-size: 16px
    font-weight: 600
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    cursor: pointer
    &::before
      content: svg-load('../icons/svg-inline/staple.svg')
