.account
  padding: 0

  &-content
    max-width: 1000px
    width: 100%
    margin: 0 auto
    display: grid
    grid-template-columns: 1fr 465px 1fr
    grid-gap: 30px

  &-item

    &:nth-child(1)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          left: calc(100% + 30px)
          size: 100px 35px
          transform: translateY(-25%)
          background-image: svg-load('../icons/svg-inline/account-line-1.svg')
          background-size: contain
    &:nth-child(2)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          right: calc(100% + 30px)
          size: 100px 35px
          transform: scale(-1, 1) translateY(-25%)
          background-image: svg-load('../icons/svg-inline/account-line-1.svg')
          background-size: contain
    &:nth-child(3)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          left: calc(100% + 30px)
          size: 65px 12px
          transform: translateY(-50%)
          background-image: svg-load('../icons/svg-inline/account-line-2.svg')
          background-size: contain
    &:nth-child(4)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          right: calc(100% + 30px)
          size: 65px 12px
          transform: translateY(-50%)
          background-image: svg-load('../icons/svg-inline/account-line-2.svg')
          background-size: contain
    &:nth-child(5)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          left: calc(100% + 30px)
          size: 100px 35px
          transform: translateY(-70%)
          background-image: svg-load('../icons/svg-inline/account-line-3.svg')
          background-size: contain
    &:nth-child(6)
      .item-image
        &::before
          position: absolute
          content: ''
          top: 50%
          right: calc(100% + 30px)
          size: 100px 35px
          transform: translateY(-70%) scale(-1, 1)
          background-image: svg-load('../icons/svg-inline/account-line-3.svg')
          background-size: contain

    &:last-child
      grid-column: 2
      grid-row: 1 / span 3
      transform: translateX(10px)

    .item
      &-image
        position: relative
        size: 60px
        margin: 0 auto
        background-image: svg-load('../icons/svg-inline/check.svg')

  .container.extended
    padding: 90px 0
    border-radius: 30px
    background-color: rgba(255, 255, 255, 0.05)
