.radio
  position: relative
  flex: 1 1 auto

  input
    position: absolute
    size: 0.1px
    visibility: hidden
    &:checked
      & + label
        background: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)

  label
    display: block
    padding: 7px 12px
    font-size: 16px
    line-height: normal
    color: $whiteColor
    text-align: center
    border-radius: 90px
    cursor: pointer

.rate-radio
  position: relative
  input
    position: absolute
    size: 0.1px
    visibility: hidden
    &:checked
      & + label
        opacity: 1

  label
    padding: 15px 25px
    display: flex
    justify-content: space-between
    align-items: center
    border-radius: 900px
    cursor: pointer
    opacity: 0.5
    span
      font-size: 18px
      font-weight: 600
      color: $whiteColor
    .ico
      size: 20px

  &.good
    label
      border: 1px solid $greenColor
      background: rgba($greenColor, 0.1)

  &.sad
    label
      border: 1px solid $redColor
      background: rgba($redColor, 0.1)
