.te-machine
  &-text
    position: absolute
    top: 50%
    left: 0
    transform: translateY(-50%)
    padding: 12px 20px
    border-radius: 90px
    font-size: 16px
    font-weight: 400
    color: $whiteColor
    background-image: linear-gradient(90deg, $blueColor 0%, $aquaColor 100%)
    & + .ui-input
      padding-left: 70px
