.partners
  &-list
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 30px

  &-item
    display: grid
    grid-template-columns: 80px auto
    grid-gap: 0 20px
    align-items: center
    img
      border-radius: 50%

+r($md)
  .partners
    &-list
      grid-template-columns: 1fr
