/* HEADER
.header
  position: fixed
  top: 0
  right: 0
  left: 0
  z-index: 9
  padding: 30px 0
  // will-change: transform
  // backface-visibility: hidden
  // transform-style: preserve-3d
  &-account
    padding: 15px 0
    background-color: rgba($whiteColor, 0.1)
    &.header-scroll
      padding: 15px 0

  &-item
    position: relative
    padding: 0 30px
    &:not(:last-child)
      &::after
        position: absolute
        content: ''
        top: 50%
        transform: translateY(-50%)
        right: 0
        size: 1px 60px
        background-color: rgba($whiteColor, 0.1)

  &-scroll
    padding: 10px 0
    background-color: $bgColor

  .assets
    padding: 10px
    display: flex
    align-items: center
    gap: 8px
    border-radius: 20px
    background: linear-gradient(154.26deg, #FCD535 16.27%, rgba(252, 213, 53, 0) 154.75%)
    .ico
      size: 30px
    span
      font-size: 20px
      font-weight: 600
      color: $whiteColor

  &-controls
    display: flex
    align-items: center

  .user
    display: flex
    align-items: center
    gap: 12px
    img
      max-width: 50px
      max-height: 50px
      border-radius: 50%

+r($md)
  .header
    &-account
      margin-top: 90px

    &-item
      padding: 0 15px

+r($sm)
  .header

    &-item
      padding: 0
      &::after
        display: none

    .controls
      display: none

    .assets
      span
        font-size: 16px

    .row
      flex-direction: row-reverse
      justify-content: space-between
