.expert
  position: relative

  &-decorations
    .decoration-1
      top: 0
      left: 50%
      transform: translate(-51%, -25%)
    .decoration-2
      top: 0
      left: 50%
      transform: translate(-49%, -25%)

  &-content
    margin-top: -90px
    counter-reset: a
    display: grid
    grid-template-columns: repeat(6, 1fr)
    grid-gap: 0px 24px

    &-wrapper
      position: relative

    &-bg
      position: absolute
      width: 75%
      top: 15%
      left: 50%
      transform: translateX(-50%)
      img
        object-fit: cover

  &-item
    position: relative
    counter-increment: a
    &::before
      position: absolute
      content: counter(a)
      top: 140px
      font-family: $titleFont
      font-size: 30px
      font-weight: 700
    &::after
      position: absolute
      content: ''
      top: -50px
      left: 50%
      transform: translateX(-50%)
      size: 30px

    .item-image
      position: relative
      display: flex
      align-items: center
      justify-content: center
      max-width: 150px
      margin: 0 auto 28px
      &::before
        content: ''
        padding-top: 100%
      .ico
        size: 52px
        color: $whiteColor

    &:nth-child(odd)
      &::before
        color: $redColor
      &::after
        background-color: $redColor
      .item-image
        background-color: $redColor
    &:nth-child(even)
      &::before
        color: $blueColor
      &::after
        background-color: $blueColor
      .item-image
        background-color: $blueColor

    // ITEM MASKS START
    &:nth-child(1),
    &:nth-child(4),
    &:nth-child(5)
      .item-image
        mask-border: url(../img/masks/exp-mask-1.svg)

    &:nth-child(2),
    &:nth-child(6)
      .item-image
        mask-border: url(../img/masks/exp-mask-2.svg)

    &:nth-child(3)
      .item-image
        mask-border: url(../img/masks/exp-mask-3.svg)
    // ITEM MASKS END

    // ITEM DECORATION MASK START

    &:nth-child(1),
    &:nth-child(3),
    &:nth-child(5)
      &::after
        mask-border: url(../img/masks/exp-min-mask-1.svg)

    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(6)
      &::after
        mask-border: url(../img/masks/exp-min-mask-2.svg)

    // ITEM DECORATION MASK END

    // ITEM POSITION START
    &:nth-child(2),
    &:nth-child(5)
      margin-top: 90%

    &:nth-child(3),
    &:nth-child(4)
      margin-top: 150%
    // ITEM POSITION END

  .section-title
    & + .text-item
      max-width: 760px
      text-align: center
      margin: 0 auto
