.history
  &-list
    min-width: 992px
  &-title-item,
  &-content-item
    display: grid
    grid-template-columns: 1fr 2fr 3fr 1fr 2fr 2fr 5fr
    align-items: center
    grid-gap: 20px
    &:not(:last-child)
      margin-bottom: 20px
      padding-bottom: 20px
      border-bottom: 1px solid rgba($whiteColor, 0.1)
