// UTILS (desktop first)

// CUSTOM MARGIN UTILS
@for $i from 0 through 20
  .mb-#{$i}
    margin-bottom: #{$i * 4}px !important
  .mt-#{$i}
    margin-top: #{$i * 4}px !important
  .my-#{$i}
    margin-top: #{$i * 4}px !important
    margin-bottom: #{$i * 4}px !important
  .ml-#{$i}
    margin-left: #{$i * 4}px !important
  .mr-#{$i}
    margin-right: #{$i * 4}px !important
  .mx-#{$i}
    margin-left: #{$i * 4}px !important
    margin-right: #{$i * 4}px !important
  .pb-#{$i}
    padding-bottom: #{$i * 4}px !important
  .pt-#{$i}
    padding-top: #{$i * 4}px !important
  .py-#{$i}
    padding-top: #{$i * 4}px !important
    padding-bottom: #{$i * 4}px !important
