/* UI
.ui-group
  position: relative
  &:not(:last-child)
    margin-bottom: 20px
  & > .ui-group
    margin-top: 15px
    margin-left: 40px
  &-input
    position: relative
  &.required
    label
      padding-left: 20px
      &::before
        position: absolute
        content: "*"
        left: 0
        color: $redColor

  label
    position: relative
    margin-bottom: 10px
    display: flex
    align-items: center
    justify-content: flex-start
    gap: 15px
    color: rgba($whiteColor, 0.5)
    .ico
      size: 18px

  .notice
    margin-top: 20px
    span
      display: inline-flex
      align-items: center
      justify-content: center
      padding: 5px 9px
      border-radius: 4px
      background-color: $bgColor2

  .placeholder-text
    position: absolute
    top: 50%
    right: 20px
    transform: translateY(-50%)
    font-size: 16px
    flex-wrap: 600
    color: $whiteColor

  .placeholder-icon
    position: absolute
    top: 50%
    right: 20px
    size: 20px
    transform: translateY(-50%)
    object-fit: contain
    color: #4DBDEBcc
    background: none
    &.normal
      opacity: 1
    &.validate
      opacity: 0

  .error
    display: none
    margin-top: 5px
    padding: 0 20px
    font-size: 14px
    font-weight: 400
    color: $redColor
    text-decoration: underline
    &.is-visible
      display: block
