.login
  padding: 20px
  min-height: 100vh
  display: flex
  justify-content: center
  align-items: center
  background-image: url(../img/login-bg.webp)
  background-size: cover

  &-wrapper
    max-width: 450px
    width: 100%

  &-form
    padding: 30px
    border-radius: 20px
    background-color: $bgColor

  .logo
    margin: 0 auto 60px

+r($sm)
  .login
    &-form
      gap: 16px
      &-row
        flex-direction: column
    .logo
      margin: 0 auto 30px
