.referal-levels
  padding: 0
  margin-bottom: 180px
  background: none

  &-percent
    position: relative
    margin: 20px 0 30px
    padding: 10px 20px
    width: fit-content
    font-size: 18px
    font-weight: 700
    color: $whiteColor
    border-radius: 0 900px 900px 0
    background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    &::before
      position: absolute
      content: url(../img/levels/before.svg)
      top: -0.5px
      left: -12px

  &-image
    size: 100px
    margin: 0 auto

  &-text
    max-width: 90%
    margin: 15px auto 30px
    color: $whiteColor
    text-align: center

  &-btn
    padding: 16px
    display: flex
    align-items: center
    justify-content: center
    gap: 10px
    width: 100%
    font-size: 16px
    font-weight: 700
    color: $whiteColor
    border-radius: 0 0 20px 20px
    .ico
      size: 12px 8px

  .swiper
    &-slide
      border-radius: 20px
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #09121A
      border: 1px solid #4B555F
    &-button
      &-next,
      &-prev
        position: absolute
        top: 50%
        size: 20px 18px
        transform: translateY(-50%)
        cursor: pointer
      &-next
        right: -50px
        background-image: svg-load('../icons/svg-inline/arrow-next-2.svg')
      &-prev
        left: -50px
        background-image: svg-load('../icons/svg-inline/arrow-prev-2.svg')

  .swiper-pagination
    display: none

  .container
    position: relative
    &::before
      position: absolute
      content: ''
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      size: calc(100% + 240px)
      border-radius: 30px
      background-color: rgba($whiteColor, 0.05)

+r($xl)
  .referal-levels
    .swiper-pagination
      display: flex

+r($lg)
  .referal-levels
    margin-bottom: 90px

    .container
      padding-left: 110px
      padding-right: 110px
      &::before
        size: calc(100% - 50px) calc(100% + 60px)

+r($sm)
  .referal-levels
    margin-bottom: 60px

    .container
      padding-left: 20px
      padding-right: 20px
      &::before
        size: 100% calc(100% + 40px)
