.referal-platinum
  padding: 0
  background: none

  &-logo
    max-width: 250px
    margin: 0 auto 40px

  &-text
    max-width: 900px
    margin: 0 auto 20px
    font-size: 20px
    font-weight: 400
    color: $whiteColor
    text-align: center

  .container
    padding-top: 50px
    padding-bottom: 50px
    border-radius: 20px
    background-color: rgba($whiteColor, 0.1)

  .btn
    margin: 0 auto
    max-width: fit-content

+r($lg)
  .referal-platinum

    .container
      max-width: calc(100% - 40px)
      margin-left: 20px
      margin-right: 20px
