.invest
  min-height: 100vh
  padding: 180px 0
  background-size: cover
  background-position: center top
  background-image: url(../img/login-bg.webp)

  &-wrapper
    border-radius: 20px
    padding: 45px 50px
    border: 1px solid $borderColor
    background-color: $bgColor3
    &:not(:last-child)
      margin-bottom: 20px

  &-image
    position: relative
    border-radius: 20px 20px 0 0
    background-color: #3399e8
    &::before
      content: ''
      display: block
      padding-top: 70%
    &-content
      position: absolute
      top: 0
      left: 0
      bottom: 0
      right: 0
      display: flex
      justify-content: center
      align-items: center
    img
      size: 120px
      object-fit: contain

  &-content
    padding: 15px 20px 20px
    border-radius: 0 0 20px 20px
    background-color: $bgColor2

  &-price
    font-size: 30px
    font-weight: 600
    text-align: center

  &-params
    padding: 15px 10px
    border-radius: 20px
    background-color: rgba($whiteColor, 0.1)

  .swiper-pagination
    display: none

+r($xl)
  .invest
    .swiper-pagination
      display: flex

+r($md)
  .invest

    &-params
      .row
        flex-direction: column
        align-items: flex-start
        gap: 5px

    &-wrapper
      padding: 30px 20px
