.container
  position: relative
  z-index: 2
  max-width: 1150px
  width: 100%
  margin-left: auto
  margin-right: auto
  padding-left: 20px
  padding-right: 20px
  &.wide
    max-width: 1630px
  &.extended
    max-width: 1390px

.row
  display: flex
  gap: 20px
  &.form-row
    padding: 5px
    border-radius: 90px
    background-color: #141d26
    gap: 5px
    &:not(:last-child)
      margin-bottom: 10px
  &.column
    flex-direction: column
  &.wrap
    flex-wrap: wrap
  &.align-center
    align-items: center
  &.align-end
    align-items: flex-end
  &.justify-center
    justify-content: center
  &.justify-between
    justify-content: space-between
  &.justify-end
    justify-content: flex-end
  &.gap-0
    gap: 0
  &.gap-10
    gap: 10px

.swiper
  &-slide
    opacity: 0
    transition: 0.25s all
    &-visible
      opacity: 1
  &-pagination
    margin-top: 30px
    display: flex
    justify-content: center
    align-items: center
    gap: 10px
    &-bullet
      size: 6px
      border-radius: 50%
      background: rgba($whiteColor, 0.2)
      &-active
        background: linear-gradient(180deg, #f73e5d 0%, rgba(247, 62, 93, 0) 100%)

.submenu
  display: none // block
  position: absolute
  top: calc(100% + 18px)
  left: 50%
  transform: translateX(-50%)
  border: 1px solid $goldColor
  border-radius: 0 0 20px 20px
  list-style-type: none
  background-color: #22313e
  overflow: hidden
  li
    padding: 20px
    &:not(:last-child)
      border-bottom: 1px solid rgba($whiteColor, 0.1)
    &:hover
      background: linear-gradient(270.77deg, rgba(250, 226, 128, 0) -5.11%, #EED780 43.1%, #C5952A 99.34%)
    a
      display: flex
      align-items: center
      gap: 8px
      text-decoration: none
      .ico
        flex: 0 0 20px
        color: $whiteColor
      span
        flex: 1 1 auto
        text-align: left
        color: $whiteColor
  .btn
    border-radius: 0 0

.tabs
  .p2p &
    &-nav
      display: flex
      border-radius: 90px
      background-color: rgba(81, 195, 235, 0.2)
      a
        display: block
        padding: 8px 20px
        font-size: 16px
        color: $whiteColor
        border-radius: 90px
        &.active
          background-image: linear-gradient(90deg, $blueColor 0%, $aquaColor 100%)
    &-item
      display: none
      &.active
        display: block

  .settings &
    &-nav
      padding: 30px 40px
      border: 1px solid $borderColor
      border-radius: 20px
      background: $bgColor3
      a
        position: relative
        display: flex
        align-items: center
        gap: 10px
        padding: 15px 0
        &::before
          position: absolute
          content: ''
          size: calc(100% + 80px) 100%
          top: 0
          left: -40px
          background-image: linear-gradient(90deg, $redColor 0%, rgba(247, 62, 93, 0) 100%)
          opacity: 0
        &:hover,
        &.active
          &::before
            opacity: 1
        span,
        .ico
          position: relative
          color: $whiteColor
        .ico
          size: 20px
    &-content
      padding: 25px 30px
      border: 1px solid $borderColor
      border-radius: 20px
      background: $bgColor3
    &-item
      display: none
      &.active
        display: block

table.p2p-table-layout
  white-space: nowrap
  width: 100%
  border-collapse: collapse
  .btn
    min-width: 120px
    max-width: 120px
  thead
    border-radius: 90px
    background-color: #1f3345
    th
      padding: 10px
      font-size: 16px
      font-weight: 400
      color: rgba($whiteColor, 0.5)
      text-align: left
      &:first-child
        padding-left: 30px
        border-radius: 90px 0 0 90px
      &:last-child
        width: 120px
        padding-right: 30px
        border-radius: 0 90px 90px 0

  tbody
    tr
      border-bottom: 1px solid rgba($whiteColor, 0.2)
      &:last-child
        border-bottom: none
    th
      text-align: left
    td
      text-align: left
      padding: 20px 10px
      font-size: 16px
      font-weight: 600
      color: $whiteColor
      &:last-child
        padding: 20px 0px 20px 10px
      &.complete
        color: $greenColor
      &.denied
        color: #BF0023
      &.active
        button
          position: relative
          padding: 0
          width: fit-content
          font-size: 16px
          font-weight: 600
          color: $blueColor
          background: none
          &::after
            position: absolute
            content: ''
            top: 50%
            right: -20px
            transform: translateY(-50%)
            size: 15px
            background-image: svg-load('../icons/svg-mono/pencil.svg', fill=rgba($whiteColor, 0.5))
            background-size: cover
      &.stoped
        button
          position: relative
          padding: 0
          width: fit-content
          font-size: 16px
          font-weight: 600
          color: $yellowColor
          background: none
          &::after
            position: absolute
            content: ''
            top: 50%
            right: -20px
            transform: translateY(-50%)
            size: 15px
            background-image: svg-load('../icons/svg-mono/pencil.svg', fill=rgba($whiteColor, 0.5))
            background-size: cover
      a
        @extend td
        display: block
        padding: 0 !important

.get-money
  &-value
    margin-bottom: 20px
    font-size: 30px
    font-weight: 600
    color: $whiteColor
  &-slider
    margin-bottom: 20px
  &-buttons
    display: flex
    align-items: center
    justify-content: space-between
    gap: 10px
    button
      flex: 1 1 auto
      padding: 20px
      border-radius: 20px
      background-color: rgba(57, 162, 233, 0.2)

.tooltip
  &-container
    position: relative
    height: 18px
    & > span,
    & > a
      cursor: pointer
    &:hover
      & > span,
      & > a
        .ico
          color: $aquaColor
      .tooltip-items
        display: block

    .ui-group label &
      position: static

  &-items
    position: absolute
    z-index: 3
    top: calc(100% + 10px)
    left: 0
    display: none
    width: 335px
    padding: 10px 15px
    border-radius: 20px
    background-color: $bgColor
    box-shadow: 5px 5px 55px rgba($whiteColor, 0.3)

    .p2p-table-layout &,
    .p2p-table-grid &
      top: unset
      bottom: 100%

    .partners-item &
      width: fit-content

    .row
      &:not(:last-child)
        margin-bottom: 10px
    .title,
    .value
      flex: 0 0 calc(50% - 10px)
    .title
      font-size: 14px
      -webkit-background-clip: text
      -webkit-text-fill-color: transparent
      background-image: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)
    .value
      font-size: 14px
      font-weight: 700
      color: $whiteColor
      &-2
        @extend .value
        font-weight: 400

+r($lg)
  .tabs
    .settings &
      &-nav
        border: none
        padding: 0
        nav
          display: none

+r($sm)
  .container
    padding-left: 10px
    padding-right: 10px

  .tabs
    .p2p &
      &-nav
        a
          display: none
