select.custom-select
  display: none

.custom-select

  .p2p-table-layout &
    &-container
      &.is-open
        .custom-select-panel
          border: none
    &-opener
      padding: 0
      background: none
      &::before
        position: absolute
        content: ''
        z-index: 4
        top: 100%
        width: 100%
        border-bottom: 1px dashed rgba($whiteColor, 0.5)
      &::after
        background-image: svg-load('../icons/svg-mono/arrow-down.svg', fill=rgba($whiteColor, 0.5))
        right: 0
    &-panel
      padding: 6px 14px 0
      margin: 0 -14px
      width: calc(100% + 28px)
      background-color: #1f3345
    &-option
      padding: 6px 0
      color: rgba($whiteColor, 0.5)
      cursor: pointer
      &.has-focus,
      &.is-selected
        font-weight: inherit
        color: $whiteColor

  .p2p .tabs-nav &
    &-container
      display: none
    &-opener
      color: $whiteColor
      background-image: linear-gradient(90deg, #298be7 0%, #70efee 100%)
    &-option
      &.has-focus,
      &.is-selected
        background-image: linear-gradient(90deg, #298be7 0%, #70efee 100%)

  .modal-form &

    &-opener
      color: $whiteColor

  .calculator &
    &-container
      &.is-open
        border-radius: 20px 20px 0 0
        background: #141D26
    &-opener
      padding: 8px 24px 8px 12px
      color: $whiteColor
      background: none
      &::after
        right: 4px
        background-image: svg-load('../icons/svg-inline/arrow-down.svg')
    &-panel
      background: #141D26
    &-option
      padding: 13px 10px

  .settings .tabs-nav &
    &-container
      display: none
    &-opener
      background-image: linear-gradient(90deg, $redColor 0%, rgba(247, 62, 93, 0) 100%)
      color: $whiteColor
    &-option
      &.has-focus,
      &.is-selected
        background-image: linear-gradient(90deg, $redColor 0%, rgba(247, 62, 93, 0) 100%)

  .p2p-order-form-select &
    &-container
      &.is-open
        .custom-select-opener
          border-color: $redColor $redColor transparent $redColor
        .custom-select-panel
          border: 1px solid $redColor
    &-opener
      border: 1px solid $redColor
  &-container
    position: relative
    // flex: 1 0 150px
    &.is-open
      .custom-select-opener
        border-radius: 20px 20px 0 0
        &::after
          transform: translateY(-50%) rotate(180deg)
      .custom-select-panel
        border-top: 1px solid rgba($whiteColor, 0.2)
        display: block
  &-opener
    position: relative
    display: block
    width: 100%
    padding: 13px 40px 13px 20px
    border-radius: 90px
    background-color: $bgColor2
    color: rgba($whiteColor, 0.5)
    cursor: pointer
    &::after
      position: absolute
      content: ''
      top: 50%
      right: 20px
      transform: translateY(-50%)
      size: 13px 8px
      background-image: svg-load('../icons/svg-mono/arrow-down.svg', fill=$whiteColor)
      background-size: cover
  &-panel
    display: none
    position: absolute
    z-index: 3
    width: 100%
    border-radius: 0 0 20px 20px
    background-color: $bgColor2
    overflow: hidden
  &-option
    padding: 10px 20px
    color: $whiteColor
    cursor: pointer
    &.has-focus,
    &.is-selected
      font-weight: 700

+r($lg)
  .custom-select

    .settings .tabs-nav &
      &-container
        display: block

+r($sm)
  .custom-select

    .p2p .tabs-nav &
      &-container
        width: 290px
        display: block
