.control-stat
  &-item
    display: flex
    align-items: center
    justify-content: space-between
    gap: 10px
    margin-bottom: 20px
    padding-bottom: 20px
    border-bottom: 1px solid rgba($whiteColor, 0.1)
  .btn
    font-size: 35px
    font-weight: 700
    line-height: normal
    min-width: 260px
    width: fit-content

+r($sm)
  .control-stat
    &-item
      flex-direction: column
      align-items: flex-start
