.team
  position: relative

  &-decorations
    .decoration-1
      &:first-child
        top: 0
        left: 0
        transform: translateX(-50%)
      &:last-child
        bottom: 0
        right: 0
        transform: translate(50%, 50%)

  &-row
    gap: 100px

  &-image
    flex: 0 0 680px

  .text-item
    max-width: 600px
    &:not(:last-child)
      margin-bottom: 24px

  .image-mask
    mask-border: url(../img/masks/mask.svg)
