.settings
  min-height: 100vh
  padding: 190px 0
  background-image: url(../img/login-bg.webp)
  background-size: cover
  background-position: center top
  &-main-panel
    display: grid
    grid-template-columns: 7fr 17fr
    grid-gap: 60px

  &-form
    &-row
      display: flex
      gap: 0 20px
      align-items: flex-end
      .ui-group
        margin-bottom: 20px
        flex: 1 0 calc(50% - 10px)
      .btn
        margin-bottom: 20px
    .btn
      min-width: 210px
      width: fit-content

+r($lg)
  .settings
    &-main-panel
      grid-template-columns: 1fr
      grid-gap: 20px
    &-form
      &-row
        flex-direction: column
        align-items: unset
        .v-hidden
          display: none
      .btn
        width: 100%
