.invest-account-stats
  padding: 0
  background: none

  &-wrapper
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 30px

  &-list
    padding: 20px
    height: 75%
    border-radius: 20px
    border: 1px solid $borderColor
    background-color: $bgColor5
    .item
      display: grid
      grid-template-columns: 2fr 1fr
      grid-gap: 10px

      &:not(:last-child)
        margin-bottom: 10px

  iframe
    width: 100%
    height: 100%

+r($lg)
  .invest-account-stats
    &-list
      .item
        grid-template-columns: 1fr
        &:not(:last-child)
          margin-bottom: 20px

+r($sm)
  .invest-account-stats
    &-wrapper
      grid-template-columns: 1fr
