.line-marketing
  background: none
  padding: 0
  margin-bottom: 60px

  &__box
    background: rgba(255, 255, 255, 0.1)
    border-radius: 20px
    padding: 27px 40px 63px
    color: $whiteColor

  &__title
    font-size: 40px
    line-height: 54px
    text-align: center
    margin-bottom: 34px

  &__row
    display: flex
    justify-content: space-between

    &:not(:last-child)
      margin-bottom: 30px

    +r($md)
      display: block

  &__col
    padding: 25px
    border-radius: 20px
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #09121A
    border: 1px solid #4B555F
    overflow: auto
    font-size: 16px
    line-height: 22px

    &-title
      font-size: 18px
      line-height: 140%
      margin-bottom: 12px

    &_col1
      width: 39%
      max-width: 39%

    &_col2
      width: 58%
      max-width: 58%

    &_text
      text-align: center
      font-weight: 600
      font-size: 16px
      line-height: 161.68%

    +r($md)
      width: 100%
      max-width: 100%

      &:not(:last-child)
        margin-bottom: 30px

  &__contracts-table
    width: 100%
    border-collapse: collapse

    td
      padding: 0 5px 8px 0
      &:first-child
        width: 95px

  &__contracts-list
    list-style: none

    li
      &:not(:last-child)
        margin-bottom: 14px

      &::before
        content: ''
        display: inline-block
        vertical-align: top
        margin: 6px 10px 0 0
        width: 8px
        height: 8px
        border-radius: 50%
        background: linear-gradient(180deg, #298BE7 0%, #70EFEE 100%)

  &__bonus-table
    font-weight: bold
    width: 100%
    border-collapse: collapse

    &-inner
      width: 100%
      display: flex
      justify-content: space-between

    &-arrow
      display: inline-block
      vertical-align: top
      width: 20px
      height: 18px
      background-image: svg-load('../icons/svg-inline/arrow-next-2.svg')
      margin-top: 2px

    th
      font-size: 18px
      text-align: left
      padding-bottom: 20px

    td
      vertical-align: middle
      padding: 0 0 10px
      width: 30%

      &:first-child
        width: 40%

  +r($mobile-s)
    th
      font-size: 14px
    th:nth-child(2),
    td:nth-child(2)
      display: none
