// for input or textarea element
.ui-input
  padding: 12px 20px
  width: 100%
  font-size: 16px
  color: $whiteColor
  border: 1px solid transparent
  border-radius: 90px
  background-color: $bgColor2
  &::placeholder
    color: rgba($whiteColor, 0.5)

  &[type="number"]
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button
      -webkit-appearance: none
  &[disabled]
    background-color: #414b54

  .ui-group-input &
    padding: 12px 60px 12px 20px

  &.not-valid
    border-color: $redColor
    & + .normal
      color: $redColor

  &.valid
    & + .normal
      opacity: 0
    & ~ .validate
      opacity: 1
      color: $greenColor

.datepicker
  display: flex
  align-items: center
  gap: 10px
  input[name="datepicker"]
    position: relative
    padding: 0
    height: 45px
    width: fit-content
    border: none
    color: rgba($whiteColor, 0.5)
    background: none
    cursor: pointer

textarea
  resize: none
  height: 100px
  &.ui-input
    border-radius: 20px
