@use "sass:color"

$choices-font-size-lg: 16px !default
$choices-font-size-md: 14px !default
$choices-font-size-sm: 12px !default
$choices-guttering: 24px !default
$choices-border-radius: 2.5px !default
$choices-border-radius-item: 20px !default
$choices-bg-color: #f9f9f9 !default
$choices-bg-color-disabled: #eaeaea !default
$choices-bg-color-dropdown: #fff !default
$choices-text-color: #333 !default
$choices-keyline-color: #ddd !default
$choices-primary-color: #00bcd4 !default
$choices-disabled-color: #eaeaea !default
$choices-highlight-color: $choices-primary-color !default
$choices-button-dimension: 8px !default
$choices-button-offset: 8px !default
$choices-icon-cross: url("data:image/svg+xml;base64,") !default
$choices-icon-cross-inverse: url("data:image/svg+xml;base64,") !default
$choices-z-index: 1

%choices-dropdown
  visibility: hidden
  z-index: $choices-z-index
  position: absolute
  width: 100%
  background-color: $bgColor2
  // border: 1px solid $choices-keyline-color;
  border-top: 1px solid rgba($whiteColor, 0.5)
  top: 100%
  margin-top: -1px
  border-bottom-left-radius: 20px
  border-bottom-right-radius: 20px
  overflow: hidden
  word-break: break-all
  will-change: visibility
  &.is-active
    visibility: visible
  .is-open &
    border-color: color.adjust($choices-keyline-color, $lightness: -15%)
  .is-flipped &
    top: auto
    bottom: 100%
    margin-top: 0
    margin-bottom: -1px
    border-radius: 0.25rem 0.25rem 0 0
  .choices
    &__list
      position: relative
      max-height: 300px
      overflow: auto
      -webkit-overflow-scrolling: touch
      will-change: scroll-position
    &__item
      position: relative
      padding: 10px
      font-size: 16px
      color: $whiteColor
      [dir="rtl"] &
        text-align: right
      &--selectable
        &.is-highlighted
          background-image: linear-gradient(90deg, $blueColor 0%, $aquaColor 100%)
          &::after
            opacity: 0.5
        @media (min-width: 640px)
          // padding-right: 100px
          // &::after
          //   content: attr(data-select-text)
          //   font-size: $choices-font-size-sm
          //   opacity: 0
          //   position: absolute
          //   right: 10px
          //   top: 50%
          //   transform: translateY(-50%)
          [dir="rtl"] &
            text-align: right
            padding-left: 100px
            padding-right: 10px
            &::after
              right: auto
              left: 10px

.choices
  position: relative
  overflow: hidden
  // margin-bottom: $choices-guttering;
  font-size: $choices-font-size-lg

  &:focus
    outline: none

  &:last-child
    margin-bottom: 0

  [hidden]
    display: none !important

  &.is-open
    overflow: visible

  &.is-disabled
    .choices__inner,
    .choices__input
      background-color: $choices-bg-color-disabled
      cursor: not-allowed
      user-select: none
    .choices__item
      cursor: not-allowed

  &__list
    margin: 0
    padding-left: 0
    list-style: none
    &--single
      display: inline-block
      // padding: 4px 16px 4px 4px
      width: 100%
      [dir="rtl"] &
        padding-right: 4px
        padding-left: 16px
      .choices__item
        width: 100%
    &--multiple
      display: inline
      .choices__item
        display: inline-block
        vertical-align: middle
        border-radius: $choices-border-radius-item
        padding: 4px 10px
        font-size: $choices-font-size-sm
        font-weight: 500
        margin-right: 3.75px
        margin-bottom: 3.75px
        background-image: linear-gradient(90deg, $blueColor 0%, $aquaColor 100%)
        // background-color: $choices-primary-color;
        // border: 1px solid color.adjust($choices-primary-color, $lightness: -5%);
        color: #fff
        word-break: break-all
        box-sizing: border-box
        &[data-deletable]
          padding-right: 5px
        &.is-highlighted
          background-color: color.adjust($choices-primary-color, $lightness: -5%)
          border: 1px solid color.adjust($choices-primary-color, $lightness: -10%)
        [dir="rtl"] &
          margin-right: 0
          margin-left: 3.75px
        .is-disabled &
          background-color: color.adjust($choices-disabled-color, $lightness: -25%)
          border: 1px solid color.adjust($choices-disabled-color, $lightness: -35%)

    &--dropdown
      @extend %choices-dropdown
      .p2p-table-layout &
        width: calc(100% + 28px)
        left: -14px
        border: none
        padding: 6px 14px 0
        background-color: #1f3345
        .choices__item
          padding: 10px 0
    &[aria-expanded]
      @extend %choices-dropdown

  &__item
    cursor: default
    &--selectable
      cursor: pointer
    &--disabled
      cursor: not-allowed
      user-select: none
      opacity: 0.5

  &__heading
    font-weight: 600
    font-size: $choices-font-size-sm
    padding: 10px
    border-bottom: 1px solid color.adjust($choices-keyline-color, $lightness: 10%)
    color: color.adjust(#333, $lightness: 30%)

  &__button
    text-indent: -9999px
    appearance: none
    border: 0
    background-color: transparent
    background-repeat: no-repeat
    background-position: center
    cursor: pointer
    &:focus
      outline: none

  &__inner
    display: inline-block
    vertical-align: middle
    width: 100%
    background-color: $bgColor2
    padding: 7.5px 7.5px 3.75px
    border-radius: 90px
    font-size: 16px
    color: rgba($whiteColor, 0.5)
    min-height: 44px
    overflow: hidden
    .is-focused &,
    .is-open &
      border-color: color.adjust($choices-keyline-color, $lightness: -15%)
    .is-open &
      border-radius: 20px 20px 0 0
    .is-flipped.is-open &
      // border-radius: 0 0 $choices-border-radius $choices-border-radius

  &__input
    display: inline-block
    vertical-align: baseline
    // background-color: $choices-bg-color;
    background-color: $bgColor2
    font-size: $choices-font-size-md
    // margin-bottom: 5px;
    border: 0
    border-radius: 0
    max-width: 100%
    // padding: 4px 0 4px 2px;
    &:focus
      outline: 0
    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration
      display: none
    &::-ms-clear,
    &::-ms-reveal
      display: none
      width: 0
      height: 0
    [dir="rtl"] &
      padding-right: 2px
      padding-left: 0

  &__placeholder
    // opacity: 0.5

  &[data-type*='select-one']
    cursor: pointer
    &::after
      content: ""
      position: absolute
      size: 13px 8px
      right: 11.5px
      top: 50%
      margin-top: -2.5px
      pointer-events: none
      background-image: svg-load('../icons/svg-mono/arrow-down.svg', fill=$whiteColor)
    .p2p-table-layout &
      &::after
        right: 0
        background-image: svg-load('../icons/svg-mono/arrow-down.svg', fill=rgba($whiteColor, 0.5))
    &.is-open
      &::after
        transform: rotate(180deg)
    &[dir="rtl"]
      &::after
        left: 11.5px
        right: auto
      .choices__button
        right: auto
        left: 0
        margin-left: 25px
        margin-right: 0
    .choices
      &__inner
        padding: 13px 40px 13px 20px
        .p2p-table-layout &
          padding: 0
          min-height: 0
          background: none
          border-radius: 0
          border-bottom: 1px dashed rgba($whiteColor, 0.5)
      &__input
        display: block
        width: 100%
        padding: 10px
        border-bottom: 1px solid $choices-keyline-color
        background-color: #fff
        margin: 0
      &__button
        background-image: $choices-icon-cross-inverse
        padding: 0
        background-size: 8px
        position: absolute
        top: 50%
        right: 0
        margin-top: -10px
        margin-right: 25px
        height: 20px
        width: 20px
        border-radius: 10em
        opacity: 0.25
        &:hover,
        &:focus
          opacity: 1
        &:focus
          box-shadow: 0 0 0 2px $choices-highlight-color
      &__item[data-value=''] .choices__button
        display: none
      &__item
        &.is-highlighted
          font-weight: 700
          background: none

  &[data-type*='select-multiple'],
  &[data-type*='text']
    &.is-open
      .choices
        &__inner
          border-radius: 20px 20px 0 0
    .choices
      &__inner
        background-color: $bgColor2
        padding: 13px 20px
        border: none
        border-radius: 90px
        cursor: text
      &__button
        position: relative
        display: inline-block
        margin-top: 0
        margin-right: -$choices-button-offset * 0.5
        margin-bottom: 0
        margin-left: $choices-button-offset
        padding-left: $choices-button-offset * 2
        border-left: 1px solid color.adjust($choices-primary-color, $lightness: -10%)
        background-image: $choices-icon-cross
        background-size: $choices-button-dimension
        width: $choices-button-dimension
        line-height: 1
        opacity: 0.75
        border-radius: 0
        &:hover,
        &:focus
          opacity: 1
