/* Checkbox
.ui-checkbox
  position: relative

  .p2p-table-filter-item &
    height: 45px
    display: flex
    align-items: center

  input[type="checkbox"]
    size: 0.1px
    position: absolute
    visibility: hidden
    &:checked
      & + label
        &::after
          opacity: 1

  label
    position: relative
    display: block
    padding-left: 30px
    user-select: none
    font-size: 16px
    color: $whiteColor
    cursor: pointer
    a
      text-decoration: underline
      &.gradient
        position: relative
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-image: linear-gradient(154.26deg, $redColor 16.27%, rgba(247, 62, 93, .5) 154.75%)

      // color: rgba($redColor, 0.5)
    &::before
      position: absolute
      content: ''
      top: 10px
      left: 0
      transform: translateY(-50%)
      size: 18px
      background: linear-gradient($bgColor, $bgColor) padding-box, linear-gradient(to right, $blueColor, $aquaColor) border-box
      border-radius: 4px
      border: 1px solid transparent
    &::after
      position: absolute
      content: ''
      top: 10px
      left: 0
      transform: translate(30%, -75%)
      size: 18px 14px
      background: svg-load('../icons/svg-inline/check2.svg')
      background-size: contain
      opacity: 0

// Toggler
.ui-toggle
  position: relative
  input
    position: absolute
    size: 0.1px
    visibility: hidden
    &:checked
      & + label
        &::after
          left: calc(60px - 22px - 5px)
        &::before
          background-color: $goldColor

  label
    position: relative
    display: block
    padding-left: 70px
    font-size: 16px
    font-weight: 400
    color: #c4c4c4
    cursor: pointer
    &::before
      position: absolute
      content: ''
      size: 60px 30px
      top: 50%
      left: 0
      border-radius: 90px
      transform: translateY(-50%)
      background-color: rgba($whiteColor, 0.1)
      transition: 0.25s all
    &::after
      position: absolute
      content: ''
      size: 20px
      top: 50%
      left: 5px
      transform: translateY(-50%)
      border-radius: 50%
      background-color: #72808f
      transition: 0.25s all
