.fixed-menu
  position: fixed
  z-index: 10
  top: 0
  left: 0
  bottom: 0
  padding: 30px
  display: flex
  background-color: $bgColor
  transition: 0.25s all
  overflow-y: auto
  overflow-x: hidden
  &.open
    padding: 30px 40px
    .logo
      display: block
    .fixed-menu-item
      justify-content: flex-start
      &::before
        left: -40px
        size: calc( 100% + 80px ) 100%
      span
        display: block
    .fixed-menu-partner
      display: block

  &-btn
    padding: 0
    size: 30px
    background: none
    img
      object-fit: contain

  &-wrapper
    flex: 1 0 auto
    display: flex
    flex-direction: column

  &-top
    display: flex
    align-items: flex-end
    justify-content: space-between

  &-body
    padding: 60px 0

  &-bottom
    margin-top: auto

  &-partner
    display: none
    position: relative
    color: $whiteColor
    img
      size: 100%
      object-fit: contain
    span
      position: absolute
      bottom: 15px
      left: 15px

  &-item
    position: relative
    padding: 20px 0
    width: 100%
    display: flex
    align-items: center
    justify-content: center
    gap: 8px
    text-decoration: none
    background: none
    &:hover
      &::before
        opacity: 1
    &::before
      position: absolute
      z-index: -1
      content: ''
      top: 0
      left: -30px
      size: calc( 100% + 60px ) 100%
      background-image: linear-gradient(90deg, #2780d4 0%, rgba(38, 125, 206, 0) 100%)
      opacity: 0
    .ico
      size: 18px
      color: $whiteColor
    span
      display: none
      font-size: 16px
      font-weight: 600
      color: $whiteColor

  &-submenu
    &.is-open
      .fixed-menu-submenu-content
        display: block
    &-content
      display: none

  .logo
    display: none

+r($hd)
  .fixed-menu
    bottom: unset
    padding: 25px 30px
    &.open
      bottom: 0
      .fixed-menu-body,
      .fixed-menu-bottom
        display: block

    &-body,
    &-bottom
      display: none

+r($md)
  .fixed-menu
    right: 0
    padding: 15px 10px
    &-top
      align-items: center

    .logo
      display: block
