.settings-common
  &-row
    display: flex
    // align-items: center
    justify-content: space-between
    flex-wrap: wrap
    gap: 20px
  &-column
    flex: 1 1 calc( 50% - 10px )
  &-item
    padding-bottom: 20px
    border-bottom: 1px solid rgba($whiteColor, 0.1)
    &:not(:last-child)
      margin-bottom: 20px
