.p2p-table

  &-top
    padding: 25px 30px
    border-radius: 20px 20px 0 0
    background: $bgColor4

  &-values
    padding: 25px 30px 5px
    border-radius: 0 0 20px 20px
    background-color: $bgColor3
    overflow: auto
    &::-webkit-scrollbar
      height: 7px
    &::-webkit-scrollbar-track
      background: rgba(196, 196, 196, 0.1)
    &::-webkit-scrollbar-thumb
      border-radius: 90px
      background-image: linear-gradient(154.26deg, $blueColor 16.27%, rgba(247, 62, 93, 0) 154.75%)
    .modal-content &
      padding: 0
      border-radius: 0
      background: none
    &-inner
      min-width: 992px

  &-status
    position: relative
    font-size: 18px
    font-weight: 600
    padding-left: 10px
    &::before
      position: absolute
      content: ''
      top: 50%
      left: 0
      transform: translateY(-50%)
      size: 4px
      border-radius: 50%
    &.online
      color: $greenColor
      &::before
        background-color: $greenColor

  &-filter
    &-row
      display: grid
      grid-template-columns: repeat(auto-fit,minmax(100px,1fr))
      align-items: end
      grid-gap: 30px
    &-item
      &.wide
        grid-column: span 2

  &-pagination
    padding: 0 16px
    display: flex
    align-items: center
    justify-content: flex-end
    a
      display: flex
      align-items: center
      justify-content: center
      padding: 8px 12px
      border-radius: 4px
      color: $whiteColor
      &.prev,
      &.next
        padding: 8px 20px
        border-radius: 20px
        background: #1E2B37
      &.prev
        margin-right: 20px
      &.next
        margin-left: 20px

      &:hover,
      &.active
        background: linear-gradient(180deg, $blueColor 0%, $aquaColor 100%)

  .ui-input
    padding: 12px 20px

+r($lg)
  .p2p-table
    &-filter
      &-row
        grid-template-columns: repeat(2, 1fr)

+r($sm)
  .p2p-table
    &-top
      border-radius: 20px
      margin-bottom: 20px
    &-values
      border-radius: 20px
    &-filter
      &-row
        grid-template-columns: 1fr
        grid-gap: 10px
